"use client"

import { useState } from "react"
import Image from "next/image"
import Link from "next/link"

import { Button } from "@/components/ui/button"
import { cn } from "@/lib/utils"

export interface FeatureSet {
  label: string
  title: string
  description: string
  link?: { text: string; href: string }
}

export interface FeatureSection16Props {
  title: string
  description: string
  featureSets: FeatureSet[]
  image?: { src: string; alt: string }
  headingId?: string
  sectionId?: string
  className?: string
}

export function FeatureSection16({
  title,
  description,
  featureSets,
  image,
  headingId,
  sectionId,
  className,
}: FeatureSection16Props) {
  const [activeIndex, setActiveIndex] = useState(0)
  const activeFeature = featureSets[activeIndex]

  const imageContent = image ? (
    <Image
      src={image.src}
      alt={image.alt}
      fill
      className="object-cover"
      sizes="(max-width: 768px) 100vw, 50vw"
    />
  ) : (
    <div
      className="flex h-full w-full items-center justify-center bg-muted text-sm text-muted-foreground"
      aria-hidden
    >
      Image placeholder
    </div>
  )

  return (
    <section
      id={sectionId}
      className={cn("space-y-12", className)}
      aria-labelledby={headingId}
    >
      {/* Title and description centered */}
      <div className="mx-auto max-w-2xl space-y-4 text-center">
        <h2
          id={headingId}
          className="text-3xl font-bold leading-tight tracking-tight sm:text-4xl"
        >
          {title}
        </h2>
        <p className="text-lg text-muted-foreground">{description}</p>
      </div>

      {/* Tab buttons centered */}
      <div className="flex justify-center flex-wrap gap-2">
        {featureSets.map((featureSet, index) => (
          <Button
            key={index}
            type="button"
            variant={activeIndex === index ? "default" : "outline"}
            onClick={() => setActiveIndex(index)}
            size="sm"
            className="whitespace-nowrap"
          >
            {featureSet.label}
          </Button>
        ))}
      </div>

      {/* Active feature content in two columns */}
      <div className="grid gap-12 lg:grid-cols-2 lg:items-center">
        {/* Left column: title, description, link */}
        <div className="space-y-4">
          <h3 className="text-xl font-semibold">{activeFeature.title}</h3>
          <p className="text-muted-foreground">{activeFeature.description}</p>
          {activeFeature.link && (
            <div>
              <Button asChild variant="link" className="h-auto p-0 text-sm">
                <Link href={activeFeature.link.href}>
                  {activeFeature.link.text}
                </Link>
              </Button>
            </div>
          )}
        </div>

        {/* Right column: image */}
        <div className="relative aspect-[4/3] w-full overflow-hidden rounded-lg border border-border/60 bg-muted">
          {imageContent}
        </div>
      </div>
    </section>
  )
}
